import uniTag from "@/components/uni-tag/uni-tag.vue"
import h5Copy from '@/js_sdk/junyi-h5-copy/junyi-h5-copy/junyi-h5-copy.js'

export default {
	components: {
		uniTag
	},
	data() {
		return {
			goodData: [],
			totalPrice: 0,
			totalDeliveryFee: 0,
			orderId: 0,
			province: '',
			city: '',
			zone: '',
			address: '',
			receiver: '',
			phone: '',
			orderStatus: null,
			statusMap: {
				1: '待发货',
				2: '待补发',
				3: '申请退款',
				4: '已退款',
				5: '已发货'
			},
			statusBackgroupMap: {
				1: 'default',
				2: 'warning',
				3: 'error',
				4: 'error',
				5: 'success'
			},
			deliveryNos:''
		}
	},
	onLoad(option) {
		this.orderId = parseInt(option.orderId)
		this.loadOrder()
	},
	methods: {
		//取消订单
		cancelOrder(item) {
			var that = this
			uni.showModal({
				title: '温馨提示',
				content: '确定需要申请撤单么？',
				confirmText: "确定",
				cancelText: "取消",
				success: res2 => {
					if (res2.confirm) {
						const reqData = {
							orderId: item
						}
						this.$http.post(this.$httpApi.order.cancelOrder, reqData).
						then(function(response) {
							that.$api.msg("取消订单成功")
							setTimeout(() => {
								that.loadOrder()
							}, 600)
						})
					}
				}
			});
		},
		stopPrevent() {},
		loadOrder() {
			var that = this;
			this.$http.get(this.$httpApi.order.showOrderDetail, {
				"orderId": this.orderId
			}).
			then(function(response) {
				//这里只会在接口是成功状态返回
				that.goodData = response.orderGoodInfos;
				that.totalPrice = response.amount;
				that.totalDeliveryFee = response.totalDeliveryFee;
				that.province = response.province;
				that.city = response.city;
				that.zone = response.area;
				that.address = response.detailarea;
				that.receiver = response.receiver;
				that.phone = response.phone;
				that.orderStatus = response.orderStatus;
				for (let k in that.goodData) {
					let deliveryNo = that.goodData[k].deliveryNo; 
					if(deliveryNo){
						deliveryNo += '\n'
						that.deliveryNos += deliveryNo
					}
					console.log(that.deliveryNos)
				}
				
			}).catch(function(error) {
				//这里只会在接口是失败状态返回，不需要去处理错误提示
				that.mescroll.endErr();
			});
		},
		submitOp(orderId, type) {
			let opType = type == 2 ? '申请撤单' : '催单';
			var that = this;
			uni.showModal({
				title: '温馨提示',
				content: '确定需要' + opType + '么？',
				confirmText: "确定",
				cancelText: "取消",
				success: res2 => {
					if (res2.confirm) {
						let reqData = {
							orderId: orderId,
							opType: type
						}
						this.$http.get(this.$httpApi.order.orderOperate, reqData).
						then(function(response) {
							that.$api.msg("申请成功");
						}).catch(function(error) {
							//这里只会在接口是失败状态返回，不需要去处理错误提示
						});
					}
				}
			});
		},
		copyOrderNo(deliveryNos){
			let content = deliveryNos // 复制内容，必须字符串，数字需要转换为字符串
			const result = h5Copy(content)
			if (result === false) {
				uni.showToast({
					title: '不支持',
				})
			} else {
				uni.showToast({
					title: '快递号复制成功',
					icon: 'none'
				})
			}
		}
	}
}
